import React, { useState } from 'react';
import { Container, Row, Col, Badge, NavLink } from 'reactstrap';
import ReactTooltip from 'react-tooltip';
import TableView from './TableView';
import { Event } from '../Shared/Tracking';

import questionList from '../../data';

import './styles.scss';

const images = require.context('../../icons', true);

const Table = () => {
  const [checked, setChecked] = useState(
    JSON.parse(localStorage.getItem('checkedQuestions')) ||
      new Array(questionList.length).fill(false),
  );

  const data = React.useMemo(() => questionList, []);

  const columns = React.useMemo(
    () => [
      {
        Header: 'Sort questions by name or pattern!',
        columns: [
          {
            id: 'Checkbox',
            Cell: rowInfo => {
              return (
                <input
                  type="checkbox"
                  className="checkbox"
                  name={rowInfo.row.original.name}
                  checked={checked[rowInfo.row.id] === true}
                  onChange={() => {
                    const isChecked = checked[rowInfo.row.id];
                    setChecked((checked[rowInfo.row.id] = !isChecked));

                    window.localStorage.setItem(
                      'checkedQuestions',
                      JSON.stringify(checked),
                    );
                  }}
                />
              );
            },
          },
          {
            Header: 'Name',
            accessor: 'name',
          },
          {
            Header: 'URL',
            accessor: 'url',
            Cell: cellInfo => (
              <NavLink
                target="_blank"
                href={cellInfo.row.original.url}
                onClick={() => {
                  Event(
                    'Table',
                    'Clicked url',
                    `${cellInfo.row.original.name} url`,
                  );
                }}
              >
                {cellInfo.row.original.url}
              </NavLink>
            ),
            disableSortBy: true,
          },
          {
            Header: 'Pattern',
            accessor: 'pattern',
          },
          {
            Header: 'Difficulty',
            accessor: 'difficulty',
            disableSortBy: true,
            Cell: cellInfo => (
              <Badge
                className={cellInfo.row.original.difficulty.toLowerCase()}
                pill
              >
                {cellInfo.row.original.difficulty}
              </Badge>
            ),
          },
          {
            Header: 'Companies',
            accessor: 'companies',
            Cell: cellInfo => {
              const companies = cellInfo.row.original.companies.map(company => {
                const icon = images(`./${company}.png`);
                return (
                  <img
                    key={company}
                    src={icon}
                    alt={company}
                    data-tip={company}
                  />
                );
              });

              return <Row className="companies">{companies}</Row>;
            },
            disableSortBy: true,
          },
        ],
      },
    ],
    [],
  );

  return (
    <Container className="table">
      <Col>
        <Row>
          <Col>
            <ReactTooltip />
            <TableView columns={columns} data={data} />
          </Col>
        </Row>
      </Col>
    </Container>
  );
};

export default Table;
